          <b-button variant="secondary" @click="skip()" class="float-right px-4 btn-sm mr-2 ml-2">Skip</b-button>
        </b-form>
      </b-modal>
      <div class="col-12 px-0">
        <div class="justify-content-between align-items-center border-bottom sticky-top bg-white">
          <div class="d-md-flex justify-content-between align-items-center p-3 ml-2">
            <div>
              <div class="d-flex pb-3 pb-md-0">
                <!-- <Avatar v-b-tooltip:hover :title="feedbackData.email" :username="feedbackData.name" :size="30" /> -->
                <div class="ml-3">
                  <h4 class="mb-2 pr-3 feed-mobiletitle feedbackup-texthgt">{{ feedbackData.title }}</h4>
                  <div class="d-flex align-items-center">
                    <p class="small text-gray-700 mb-0 activity-avatar d-md-flex d-block">
                      {{ feedbackData.name != "" ? feedbackData.name : "Anonymous" }} created
                      <small class="text-muted ml-1">
                        <span
                          v-b-tooltip:hover
                          :title="
                            moment
                              .utc(feedbackData.created_date)
                              .local()
                              .format('YYYY-MM-DD HH:mm:ss')
                          "
                        >
                          <VueMomentsAgo
                            prefix=""
                            :key="feedbackData.created_date"
                            :date="
                              moment
                                .utc(feedbackData.created_date)
                                .local()
                                .format('YYYY-MM-DD HH:mm:ss')
                            "
                            lang="en"
                          />
                        </span>
                      </small>
                    </p>
                    <div class="d-none d-md-block feedmobile-card ml-2">
                      <badge v-if="feedbackData.type == 'Bug'" class="badge-soft-danger badge p-2">{{ feedbackData.type }}</badge>
                      <badge v-else-if="feedbackData.type == 'Feedback'" class="badge-soft-warning badge p-2">{{ feedbackData.type }}</badge>
                    </div>
                    <div class="feedmobile-card ml-2 ">
                      <badge v-if="feedbackData.status == 'Open' || feedbackData.status == 'New'" class="badge-soft-primary badge  p-2 ">{{ feedbackData.status }}</badge>
                      <badge v-else-if="feedbackData.status == 'In Works' || feedbackData.status == 'Planned'" class="badge badge-soft-warning  p-2">{{ feedbackData.status }}</badge>
                      <badge v-else-if="feedbackData.status == 'Closed'" class="badge badge-soft-success  p-2">{{ feedbackData.status }}</badge>
                      <badge v-else class="badge badge-soft-danger  p-2">{{ feedbackData.status }}</badge>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div>
              <div class="d-flex align-items-center ml-3 ml-md-0">
                <template>
                  <b-button :disabled="fIndex == 0" v-if="fdb_len > 0" @click="next_previous_task('previous')" size="sm" variant="white" class="border">
                    <span class="fe fe-chevron-left align-middle" />
                  </b-button>
                  <span class="mx-3">{{ fIndex + 1 }} of {{ fdb_len }}</span>
                  <b-button :disabled="fIndex == fdb_len - 1" v-if="fdb_len > 1" @click="next_previous_task('next')" size="sm" class="border mr-md-3" variant="white">
                    <span class="fe fe-chevron-right align-middle" />
                  </b-button>
                </template>
                <button type="button" class="close text-dark close-mobilebutton" @click="hide">
                  <svg viewBox="0 0 16 16" width="1em" height="1em" focusable="false" role="img" aria-label="x" xmlns="http://www.w3.org/2000/svg" fill="currentColor" class="bi-x b-icon bi">
                    <g>
                      <path
                        d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z"
                      ></path>
                    </g>
                  </svg>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>

      <b-col lg="12" class="col-lg-12 comment-detail mt-4 px-4 feedback-mobileimage">
        <b-card class="feedback-card-height shadow-none border-0 card-inactive" body-class="p-0">
          <div class="mb-5 feedback-messages feedmobile-card-break" v-if="feedbackData.description.length > 0">
            <p v-if="feedbackData.description.length > 100" class="text-gray-900 mb-5 card shadow-none p-3">
              <span v-html="feedbackData.description" class="feedbackup-height"></span>
            </p>
            <p v-else class="text-gray-900 card mb-5 shadow-none p-3">
              <span v-html="feedbackData.description" class="feedbackup-height"></span>
            </p>
          </div>
          <div class="d-flex justify-content-center w-100" v-if="loading">
            <b-spinner class="my-5" variant="primary" label="Spinning"></b-spinner>
          </div>
          <div v-if="!loading" class="col-12 comment-feedbackup-details">
            <div v-if="comments_data && comments_data.length > 0" class="mt-3 mb-6 pb-5 mx-n3 comment-height">
              <div class="comment mb-3" v-for="(comment, index) in comments_data" :key="index">
                <div class="row">
                  <div class="col-auto pr-0">
                    <!-- Avatar -->
                    <Avatar v-b-tooltip:hover :title="comment.visitor_email ? comment.visitor_email : 'Anonymous'" :username="comment.visitor_email ? comment.visitor_email : 'Anonymous'" :size="30" />
                  </div>
                  <div class="col ms-n2 mr-2">
                    <!-- Body -->
                    <div class="comment-body w-100 feedback-mobileimage">
                      <div class="row mb-2">
                        <div class="col">
                          <!-- Title -->
                          <div class="onhover position-relative">
                            <h4 class="comment-title mb-1">
                              {{ comment.visitor_name != "" ? comment.visitor_name : "Anonymous" }}
                              <span class="fe fe-edit-3 text-muted ml-2 d-none icon" @click="editRow(comment, index)" v-b-tooltip:hover title="Edit" v-if="comment.commented_by == visitor_id"></span>
                            </h4>
                          </div>
                        </div>
                        <div class="col-auto">
                          <!-- Time -->
                          <p class="small text-gray-700 mb-0 activity-avatar d-md-flex d-block">
                            {{ comment.title }}
                            <small class="text-muted ml-1">
                              <span
                                v-b-tooltip:hover
                                :title="
                                  moment
                                    .utc(comment.created_date)
                                    .local()
                                    .format('YYYY-MM-DD HH:mm:ss')
                                "
                              >
                                <VueMomentsAgo
                                  prefix=""
                                  :key="comment.created_date"
                                  :date="
                                    moment
                                      .utc(comment.created_date)
                                      .local()
                                      .format('YYYY-MM-DD HH:mm:ss')
                                  "
                                  lang="en"
                                />
                              </span>
                            </small>
                          </p>
                        </div>
                      </div>
                      <!-- / .row -->

                      <!-- Text -->
                      <p class="comment-text font-size-base text-body" v-if="comment.id != edit_form.id">
                        <span class="word-break align-top mt-3">
                          <p class="text-body discussion-text message-text feedback-comment-web" v-html="comment.description"></p>
                        </span>
                      </p>
                      <div v-else>
                        <QuillEditor :value="edit_form.description" @quill_close="quill_close()" ref="myQuillEditor" />
                        <div class="">
                          <b-button @click="updateComment(visitor_id)" variant="primary" size="sm" class="float-right update-btn px-3" v-if="!disabled">{{ edit_btn_update }}</b-button>
                          <b-button @click="updateComment(visitor_id)" variant="primary" size="sm" class="float-right px-3 update-btn" disabled v-if="disabled">{{ edit_btn_update }}</b-button>
                          <p v-if="edit_error_show" :class="edit_error_cls + ' float-right mr-3 mt-2'">{{ edit_error_msg }}</p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <!-- / .row -->
              </div>
            </div>
            <div class="bg-white fixed-bottom p-4 quill editormobile-bottom">
              <div>
                <QuillEditor ref="myQuillEditorRef" />
              </div>
              <div class="mt-4 feedbackup-savebutton">
                <b-button @click="saveComment(visitor_id)" variant="primary" class="float-right px-4" size="sm" v-if="!disabled">{{ btn_save }}</b-button>
                <b-button @click="saveComment(visitor_id)" variant="primary" class="float-right px-4" size="sm" disabled v-if="disabled">{{ btn_save }}</b-button>
                <p v-if="error_show" :class="error_cls + ' float-right mr-3 mt-2 position-absolute z-index-99 comment-validation badge badge-soft-danger'">{{ error_msg }}</p>
              </div>
            </div>
          </div>
        </b-card>
      </b-col>
    </div>
  </div>
</template>
<script>
  // import moment from "moment";
  import VueMomentsAgo from "@/components/VueMoments.vue";
  import QuillEditor from "./QuillEditor.vue";
  import Comments from "@/js/feedbackup/comments";
  import FeedbackUp from "@/js/feedbackup/feedbackup";
  import Avatar from "vue-avatar";
  export default {
    components: { VueMomentsAgo, Avatar, QuillEditor },
    props: {
      feedbackData: Object,
      visitor_id: String,
      fIndex: Number,
      fdb_len: Number, // Feedback data length
    },
    data() {
      return {
        error_show: false,
        error_cls: "text-success",
        error_msg: "",
        btn_save: "Add Comment",
        disabled: false,
        defaultPic: "https://infinity.500apps.com/img/default_avatar.png",
        loading: true,
        visitor_form: {
          name: "",
          email: "",
        },
        form: {
          description: "",
          commented_by: this.visitor_id,
          domain_id: window.domain,
          created_by: window.user, // Its need for activity
          feedback_id: this.feedbackData.id,
        },
        comments_data: [],
        edit_error_show: false,
        edit_error_cls: "text-success",
        edit_error_msg: "",
        edit_btn_update: "Update",
        edit_index: 0,
        edit_form: {},
        editData: {},
        enable: false,
      };
    },
    async mounted() {
      this.getComments();
    },
    methods: {
      quill_close() {
        this.enable = true;
      },
      closeQuill() {
        this.$set(this.comments_data, this.edit_index, this.editData);
        this.edit_form = {};
      },
      editRow(data, index) {
        let self = this;
        document.addEventListener("click", () => {
          if (document.activeElement.className !== "ql-editor" && self.enable) {
            this.closeQuill();
          }
        });
        this.enable = false;
        this.editData = data;
        this.edit_index = index;
        this.edit_form = {};
        this.edit_form.description = data.description;
        this.edit_form.id = data.id;
        this.edit_form.commented_by = this.visitor_id;
        this.edit_form.feedback_id = data.feedback_id;
        this.edit_form.created_by = window.user; // Its need for activity
        this.$refs.myQuillEditor.description = data.description;
      },
      async updateComment() {
        if (
          !this.$refs.myQuillEditor[0].description ||
          (this.$refs.myQuillEditor[0].description.replace(/( |<([^>]+)>)/gi, "").trim() == "" && !this.$refs.myQuillEditor[0].description.includes("<img"))
        ) {
          this.edit_error_show = true;
          this.edit_error_cls = "text-danger";
          this.edit_error_msg = "Please add comment.";
          setTimeout(() => {
            this.error_show = false;
            this.error_cls = "";
          }, 1500);
          return;
        } else {
          this.edit_btn_update = "Updating";
          this.edit_form.description = this.$refs.myQuillEditor[0].description;
          await Comments.updateComment(this.edit_form);
          this.edit_btn_update = "Updated";
          this.edit_error_show = true;
          this.edit_error_cls = "text-success";
          this.edit_error_msg = "Comment update successfully!!.";
          setTimeout(() => {
            this.edit_error_show = false;
            this.edit_error_cls = "";
            this.edit_error_msg = "";
          }, 1500);
          let updtaedData = await Comments.getDetails(this.edit_form.id, this.feedbackData.id);
          this.edit_btn_update = "Update";
          this.disabled = false;
          this.$set(this.comments_data, this.edit_index, updtaedData[0]);
          this.edit_form = {};
        }
      },
      hide() {
        this.$root.$emit("bv::toggle::collapse", "commentDetails");
      },
      next_previous_task(key) {
        if (key == "previous") {
          this.$parent.$parent.rowData = this.$parent.$parent.feed_back_data[this.fIndex - 1];
          this.$parent.$parent.fIndex = this.fIndex - 1;
        } else {
          this.$parent.$parent.rowData = this.$parent.$parent.feed_back_data[this.fIndex + 1];
          this.$parent.$parent.fIndex = this.fIndex + 1;
        }
        this.$parent.$parent.render++;
      },
      async getComments() {
        this.loading = true;
        let response = await Comments.getComments(this.feedbackData.id);
        this.loading = false;
        this.comments_data = response;
      },
      async onSubmit() {
        let response = await FeedbackUp.addVisitor(this.visitor_form);
        this.$bvModal.hide("visitor");
        if (response) {
          this.$parent.$parent.visitor_id = response[0].GENERATED_KEY;
          this.$parent.$parent.visitor_name = this.visitor_form.name;
        }
        this.form.commented_by = response[0].GENERATED_KEY;
        this.saveComment(response[0].GENERATED_KEY);
      },
      skip() {
        this.onSubmit();
      },
      // Add Event Listener for files
      onEditorReady(quill) {
        var files_button = quill.getModule("toolbar").container.querySelector(".ql-files");
        if (files_button) {
          files_button.addEventListener("click", () => {
            this.selectLocalImage();
          });
        }
      },
      // Emit on editor blur
      onEditorBlur(quill) {
        this.$emit("blur", quill);
        let self = this;
        self.enable = true;
      },
      // Emit on editor focus
      onEditorFocus(quill) {
        this.$emit("focus", quill);
      },
      async saveComment(key) {
        if (
          !this.$refs.myQuillEditorRef.description ||
          (this.$refs.myQuillEditorRef.description.replace(/( |<([^>]+)>)/gi, "").trim() == "" && !this.$refs.myQuillEditorRef.description.includes("<img"))
        ) {
          this.error_show = true;
          this.error_cls = "text-danger";
          this.error_msg = "Please add comment.";
          setTimeout(() => {
            this.error_show = false;
            this.error_cls = "";
          }, 1500);
          return;
        }
        this.disabled = true;
        if (key == "0") {
          this.$bvModal.show("visitor");
        } else {
          this.btn_save = "Add Commet..";
          this.form.description = this.$refs.myQuillEditorRef.description;
          let response = await Comments.addComment(this.form);
          this.btn_save = "Add Comment";
          this.error_show = true;
          this.error_cls = "text-success";
          this.error_msg = "Comment added successfully!!.";
          setTimeout(() => {
            this.error_show = false;
            this.error_cls = "";
          }, 1500);
          let insertData = await Comments.getDetails(response[0].GENERATED_KEY, this.feedbackData.id);
          this.btn_save = "Add Comment";
          this.disabled = false;
          this.comments_data.push(insertData[0]);
          this.$parent.$parent.feed_back_data[this.fIndex].comments_count++;
          this.$refs.myQuillEditorRef.description = "";
          this.form.description = "";
        }
      },
    },
  };
</script>
